import React from 'react';
import { Rate } from 'antd';
import { graphql, navigate } from 'gatsby';
import ImageGallery from 'react-image-gallery';

// Redux
import { connect } from 'react-redux';
import { add } from '../../redux/action';

import Layout from '../Layout/Layout';
import classes from './Product.module.scss';
import '../../../node_modules/react-image-gallery/styles/css/image-gallery.css';

export const query = graphql`
	query($slug: String!) {
		contentfulProduct(slug: { eq: $slug }) {
			titlemain
			descmain
			price
			img {
				file {
					url
				}
			}
			slug
			rate
		}
	}
`;

function Product({ data, add, items }) {
	const title = data.contentfulProduct.titlemain;
	const desc = data.contentfulProduct.descmain;
	const rate = data.contentfulProduct.rate;
	const price = data.contentfulProduct.price;
	const img = data.contentfulProduct.img[0].file.url;
	const returnImages = () => {
		const imgArray = [];
		data.contentfulProduct.img.forEach((each) => {
			imgArray.push({ original: each.file.url, thumbnail: each.file.url });
		});
		return imgArray;
	};
	const dataImages = returnImages();
	// If is already in basket
	const added = items.find((any) => any.title === title);
	const afzude = 'به سبد خرید افزوده شده';
	const biafzan = 'افزودن به سبد خرید';
	const btntext = added ? afzude : biafzan;
	return (
		<Layout title={title}>
			<div className="container mt-5 mb-5">
				<div className="row">
					<div className="col-lg-8 col-md-8 col-sm-12 col-12">
						<div className="mr-3 ml-3">
							<ImageGallery lazyLoad={true} showPlayButton={false} items={dataImages} />
						</div>
					</div>
					<div className="col-lg-4 col-md-4 col-sm-12 col-12">
						<h3 className="text-center mt-4">{title}</h3>
						<div className="d-flex justify-content-center pt-2 pb-2">
							<Rate style={{ color: 'black' }} allowHalf defaultValue={rate} disabled />
						</div>
						<h5 dir="rtl" className="text-center mt-3 pb-2">
							{price.toLocaleString('ar-EG')} هزارتومان
						</h5>
						<div className="d-flex justify-content-center mt-3">
							<button
								onClick={() => add({ price: price, title: title, img: img, inCart: 1 })}
								className={classes.btn}
								disabled={added}
							>
								{btntext}
							</button>
						</div>
						<p className={classes.desc}>توضیحات</p>
						<hr className="p-0" />
						<p dir="rtl" className="text-right mt-2">
							{desc}
						</p>
						<div className="d-flex justify-content-end">
							<button className={classes.back} onClick={() => navigate(-1)}>
								بازگشت به صفحه قبل
							</button>
						</div>
					</div>
				</div>
			</div>
		</Layout>
	);
}
const mapState = (state) => {
	return {
		items: state.items
	};
};
const mapDis = (dispatch) => {
	return {
		add: (item) => dispatch(add(item))
	};
};

export default connect(mapState, mapDis)(Product);
